.hero-container {
    background: url('../images/fromthesky.jpeg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}

.hero-container > h1 {
    color: #fff;
    font-size: 100px;
    margin-top: -100px;
}

.hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 32px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.hero-btns {
    margin-top: 32px;
}

.hero-btns .btn {
    margin-left: 10px;
}

@media screen and (max-width: 960px){
    .hero-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px){
    .hero-container > h1 {
        font-size: 50px;
        margin-top: -100px;
        width: 100%;
    }

    .hero-container > p {
        font-size: 22px;
    }

    .btn-mobile {
        display: block;
        font-size: 10px;
        margin-right: 22px;
        text-decoration: none;
    }

    #home {
        width: 100%;
    }

    .btn {
        width: 100%;
   }
}

@media only screen and (min-width: 310px) and (max-width: 650px ) {
    .hero-container > h1 {
        font-size: 35px;
        margin-top: -100px;
        width: 100%;
    }

    .hero-container > p {
        font-size: 18px;
    }

    .btn-mobile {
        display: block;
        font-size: 10px;
        margin-right: 22px;
        text-decoration: none;
    }

    #home {
        width: 100%;
    }

    .btn {
        width: 100%;
   }
}

@media only screen and (min-width: 280px) and (max-width: 309px ) {
    .hero-container > h1 {
        font-size: 30px;
        margin-top: -100px;
        width: 100%;
    }

    .hero-container > p {
        font-size: 15px;
    }

    .hero-btns {
        margin-top: 52px;
        font-size: 12px;
    }

    .hero-btns .btn {
        margin: 4px;
        font-size: 14px;
    }
}
