:root {
    --primary: #fff;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary {
    background-color: var(--primary);
    color: rgb(102, 205, 223);
    border: 1px solid var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    align-items: center;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large {
    padding: 12px 26px;
    align-items: center;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover, .btn--primary:hover {
    color: #fff;
    background-color: rgb(102, 205, 223);
    transition: all 0.3s ease-out;
}
